import { useContext } from "react";
import { StoreContext } from "../../../store/StoreProvider";

const RegisterPanelHead = () => {
  const { guest } = useContext(StoreContext);
  return (
    <h3 className="text-center m-3">
      {guest
        ? "Wpisz swoje dane potrzebne do wysyki i przejd dalej do zamwienia "
        : "Zarejestruj si i zacznij korzysta z przywilejw ju dzisiaj !"}
    </h3>
  );
};

export default RegisterPanelHead;
